import React, {PureComponent} from 'react';
import Label from 'r-cmui/components/Label';
import Table from 'r-cmui/components/Table';
import Row from 'r-cmui/components/Row';
import Col from 'r-cmui/components/Col';

import Demo1 from './demo1';
import Demo2 from './demo2';
import Demo3 from './demo3';


class Page extends PureComponent {
    render () {
        return (
            <div className='main-container'>
                <h1 className='page-h1'>Accordion </h1>
                <blockquote className='page-tip'>
                    /
                </blockquote>

                <h1 className='page-h1'></h1>

                <Row>
                    <Col grid={1} className='code-col'>
                        <Label className='code-box'>
                            <Demo1></Demo1>
                        </Label>
                        <Label className='code-box'>
                            <Demo2></Demo2>
                        </Label>
                        <Label className='code-box'>
                            <Demo3></Demo3>
                        </Label>
                    </Col>
                </Row>

                <h2 className='page-h2'>API</h2>
                <h3 className='page-h3'>Button</h3>

                <Table columns={[
                    {name: 'param', text: ''},
                    {name: 'desc', text: ''},
                    {name: 'type', text: ''},
                    {name: 'default', text: ''}
                ]} bordered={true} data={[
                    {param: 'theme', desc: 'default primary success warning danger', type: 'string', default: 'default'},
                    {param: 'className', desc: 'class', type: 'string', default: ''},
                    {param: 'style', desc: '', type: 'object', default: ''},
                    {param: 'onOpen', desc: '', type: 'function', default: ''},
                    {param: 'onCollapse', desc: '', type: 'function', default: ''},
                    {param: 'onOpened', desc: '', type: 'function', default: ''},
                    {param: 'onCollapsed', desc: '', type: 'function', default: ''},
                    {param: 'bordered', desc: '', type: 'boolean', default: 'false'}
                ]}></Table>

                <h3 className='page-h3'>Methods</h3>

                <ul className='code-methods'>
                    <li>
                        <i>activeByIndex(index)</i>
                        <span>index</span>
                        <ul>
                            <li>
                                index {Number} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>activeItem(item)</i>
                        <span>itemitemkey</span>
                        <ul>
                            <li>
                                item {String / Object} itemitemkey
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>getItem(key)</i>
                        <span>Item</span>
                        <ul>
                            <li>
                                key {String} itemkey
                            </li>
                            <li>
                                return {Object} item
                            </li>
                        </ul>
                    </li>
                </ul>
            </div>
        );
    }
}

export default Page;



