import React from 'react';
import BaseDemo from '../BaseDemo';
import FontIcon from 'r-cmui/components/FontIcon';
import Code from '../Code';
import Accordion from 'r-cmui/components/Accordion';
import Button from 'r-cmui/components/Button';

class Demo extends BaseDemo {
    activeByIndex () {
        this.refs.accordion.activeByIndex(1);
    }

    activeItem () {
        this.refs.accordion.activeItem('sdk');
    }

    render () {
        return (
            <div>
                <div className='code-box-demo'>
                    <Accordion style={{width: '500px'} } ref='accordion' theme='white'>
                        <Accordion.Item title='Proactive Alerting' icon='square'>
                            {`Nothing is more important than getting timely and actionable data.
                            Same as Crashlytics, we've built a layer of intelligent post-processing
                            to alert you about key events within your app as they happen.`}
                        </Accordion.Item>
                        <Accordion.Item title='Powerful Developer Account' icon='calendar'>
                            {`With Fabric, youll have a single, dedicated developer account with
                            complete access to best-in-class mobile SDKs. Simply add a few lines
                            of code and start coding your app right away. We'll even provision
                            your keys for you.`}
                        </Accordion.Item>
                        <Accordion.Item identify='sdk' title='SDK' icon='th'>
                            SDKSDK
                            
                            :SDK
                        </Accordion.Item>
                        <Accordion.Item title='' icon='ellipsis-h'>
                            Autobinding:  JavaScript  this 
                             React
                            React  CPU 
                            
                        </Accordion.Item>
                    </Accordion>

                    <div className='mt-20'>
                        <Button onClick={this.activeByIndex.bind(this)}>2</Button>
                        <Button className='ml-10' onClick={this.activeItem.bind(this)}>key</Button>
                    </div>
                </div>
                <div className='code-box-desc'>
                    <div className='code-box-title'>open</div>
                    <div>
                        Accordion.Itemopen  
                        <FontIcon icon={'chevron-circle-down'} ref='collapse' className='collapse' onClick={this.openCloseCode.bind(this)}></FontIcon>
                    </div>
                </div>
                <div className='code-box-src' ref='boxSrc'>
                    <Code className='language-jsx'>
                        {`
import Accordion from 'r-cmui/components/Accordion';
import Button from 'r-cmui/components/Button';

activeByIndex(){
    this.refs.accordion.activeByIndex(1);
}

activeItem(){
    this.refs.accordion.activeItem("sdk");
}

ReactDOM.render(
<div>
    <Accordion style={{width: '500px'} } ref="accordion" theme='white'>
        <Accordion.Item title="Proactive Alerting" icon="square">
            Nothing is more important than getting timely and actionable data.
            Same as Crashlytics, we've built a layer of intelligent post-processing
            to alert you about key events within your app as they happen.
        </Accordion.Item>
        <Accordion.Item title="Powerful Developer Account" icon="calendar">
            With Fabric, youll have a single, dedicated developer account with
            complete access to best-in-class mobile SDKs. Simply add a few lines
            of code and start coding your app right away. We'll even provision
            your keys for you.
        </Accordion.Item>
        <Accordion.Item identify="sdk" title="SDK" icon="th">
            SDKSDK
            
            :SDK
        </Accordion.Item>
        <Accordion.Item title="" icon="ellipsis-h">
            Autobinding:  JavaScript  this 
             React
            React  CPU 
            
        </Accordion.Item>
    </Accordion>

    <div className="mt-20">
        <Button onClick={this.activeByIndex.bind(this)}>2</Button>
        <Button className="ml-10" onClick={this.activeItem.bind(this)}>key</Button>
    </div>
</div>, mountNode);
`}
                    </Code>
                </div>
            </div>
        );
    }
}

export default Demo;



